import styled from "styled-components";
import { motion, AnimatePresence } from "framer-motion";
import { useState } from "react";

const Wrapper = styled.div`
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction:column;
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 50vw;
  gap: 10px;
  /*div:first-child,
  div:last-child {
    grid-column: span 2;
  }*/
`;

const Circle = styled(motion.div)`
  background-color: #ffffff;
  height: 50px;
  width: 50px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 10px 20px rgba(0, 0, 0, 0.06);
`;

const Box = styled(motion.div)`
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 10px;
  height: 200px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 10px 20px rgba(0, 0, 0, 0.06);
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Overlay = styled(motion.div)`
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Switch = styled(motion.button)`
  width: 100px;
  height:30px;
  border-radius:5px;
  display: block;
  border:none;
  font-weight:bold;
  color:rgb(64, 1, 251, 1);
  position:absolute;
  bottom:10%;
`;

const overlay = {
  hidden: { backgroundColor: "rgba(0, 0, 0, 0)" },
  visible: { backgroundColor: "rgba(0, 0, 0, 0.5)" },
  exit: { backgroundColor: "rgba(0, 0, 0, 0)" },
};
const variants = {
  hover: (custom:string)=>(
    { scale: 1.1 ,
      x : custom==="1" ? -20 : custom==="2" ? 20 : custom==="3" ? -20 : 20 ,
      y : custom==="1" ? -10 : custom==="2" ? -10 : custom==="3" ? 10 : 10 ,
    }
  )
};


function App() {
  const [id, setId] = useState<null | string>(null);
  const [clicked, setClicked] = useState(false);
  const toggleClicked = () => setClicked((prev) => !prev);

  return (
    <Wrapper>
      <Grid>
        {["1", "2", "3", "4"].map((n) => (
          <Box onClick={() => setId(n)} key={n} layoutId={n} variants={variants} whileHover="hover" custom={n}>
            {!clicked && n==="2" ? (
              <Circle layoutId="circle" style={{ borderRadius: 50 }} />
            ) : null}
            {clicked && n==="3" ? (
              <Circle layoutId="circle" style={{ borderRadius: 50 }} />
            ) : null}
          </Box>
        ))}
      </Grid>
      <AnimatePresence>
        {id ? (
          <Overlay
            variants={overlay}
            onClick={() => setId(null)}
            initial="hidden"
            animate="visible"
            exit="exit"
          >
            <Box layoutId={id} style={{ width: 400, height: 200, backgroundColor:"rgba(255, 255, 255, 1)"}} />
          </Overlay>
        ) : null}
      </AnimatePresence>
      <Switch onTapStart={toggleClicked} onTap={toggleClicked} whileTap={{color:"rgba(239, 174, 0, 1)",scale:1.1}}>Switch</Switch>
    </Wrapper>
    
);
}

export default App;